import React from 'react'
import {Route} from 'react-router'

import Products from "../../pages/Products";
import PartnersAndClients from "../../pages/PartnersAndClients"
import TradeMarks from "../../pages/TradeMarks"
import About from "../../pages/About"
import Contacts from "../../pages/Contacts"

const correctUrl = process.env.NODE_ENV === 'production' ? process.env.API_URL : '/';


export default (
    <div>
        <Products/>
        <Route exact path={correctUrl} component={Products}/>
        <Route exact path={correctUrl + "partners_and_clients"} component={PartnersAndClients}/>
        <Route exact path={correctUrl + "trade_marks"} component={TradeMarks}/>
        <Route exact path={correctUrl + "about"} component={About}/>
        <Route exact path={correctUrl + "contacts"} component={Contacts}/>
    </div>
)


